
import React, { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import { toast, ToastContainer } from "react-toastify";

import Logo from "../images/logo-1.png";
import { logout } from "../../../store/actions/user.action.js";

import { useUserProfile } from '../../../context/UserContextProvider.js';
import { useMediaQuery } from '../../../helpers/custom-hooks.js';
import { CONSTANTS } from '../../../helpers/constant.js';

const Sidebar = () => {
	const navigate = useNavigate();

	const { getUserProfile_c, logoutUser_c, refreshProfile } = useUserProfile();;

	const isDesktop = useMediaQuery('(min-width: 992px)');

	useEffect(() => {
		refreshProfile();
	}, []);

	async function logoutUser() {
		let response = await logout();
		if (response.status === 200) {
			logoutUser_c();
			toast.success("Logged Out");
			navigate("/login");
		} else if (response.status >= 400) {
			toast.warn(response.message.message);
		}
	}


	return (
		<Navbar bg="light" expand="lg" className="px-5">
			<Container className="mx-auto">
				<Navbar.Brand>
					<Link to="/">
						<img src={Logo} alt="logo" width={110} height={38} />
					</Link>
				</Navbar.Brand>
				<Navbar.Toggle aria-controls="navbarScroll" />
				<Navbar.Collapse id="navbarScroll">
					<Nav
						className="me-auto my-2 my-lg-0"
						style={{ maxHeight: "100px" }}
						navbarScroll
					>
						<NavDropdown title="Cities" id="navbarScrollingDropdown">
							{getUserProfile_c()?.role?.toLowerCase().trim() === CONSTANTS.USER ? null : (
								<NavDropdown.Item as={Link} to="/create-city">
									Add City
								</NavDropdown.Item>
							)}
							<NavDropdown.Item as={Link} to="/list-cities">
								List Cities
							</NavDropdown.Item>
						</NavDropdown>
						<NavDropdown title="Users" id="navbarScrollingDropdown">
							{getUserProfile_c().role?.toLowerCase().trim() === CONSTANTS.USER ? null : (
								<NavDropdown.Item as={Link} to="/create-user">
									Create User
								</NavDropdown.Item>
							)}
							<NavDropdown.Item as={Link} to="/list-users">
								List Users
							</NavDropdown.Item>
						</NavDropdown>

						{getUserProfile_c().role?.toLowerCase().trim() === CONSTANTS.USER ? null : (
							<NavDropdown title="Reports" id="navbarScrollingDropdown">
								<NavDropdown.Item as={Link} to="/full-report">
									Full Reports
								</NavDropdown.Item>
								<NavDropdown.Item as={Link} to="/frequency-report">
									Frequency Reports
								</NavDropdown.Item>
								<NavDropdown.Item as={Link} to="/mean-report">
									Mean (Average) Reports
								</NavDropdown.Item>
							</NavDropdown>
						)}

						{/* 
						{!isDesktop && (
							<div>
								<hr
									style={{
										background: "grey",
										color: "grey",
										borderColor: "grey",
										height: "2px",
									}}
								/>
								<div className="mx-4 mb-0 mt-3 hide-details">
									<h6 className="mb-0">{getUserProfile_c()?.name}</h6>
									<p style={{ color: "grey", fontSize: "11px" }}>{getUserProfile_c()?.role}</p>
								</div>
								<Button variant="danger" size="sm" onClick={() => logoutUser()}>
									<div className="row grid-divider"></div>
									Logout
								</Button>
							</div>
						)} */}
					</Nav>
					{isDesktop ? (
						<>
							<div className="mx-4 mb-0 mt-3">
								<Link to="/profile">
									<h5 className="mb-0">{getUserProfile_c()?.name}</h5>
								</Link>
								<p style={{ color: "grey", fontSize: "11px" }}>{getUserProfile_c()?.role === CONSTANTS.ADMIN ? CONSTANTS.RESEARCH_ASSISTANT : CONSTANTS.DATA_COLLECTOR}</p>
							</div>
							<Button variant="danger" size="sm" onClick={() => logoutUser()}>
								<div className="row grid-divider"></div>
								Logout
							</Button>
						</>
					) :
						(
							<>
								<hr
									style={{
										background: "grey",
										color: "grey",
										borderColor: "grey",
										height: "2px",
									}}
								/>
								<div className="mx-4 mb-0 mt-3 hide-details">
									<Link to="/profile">
										<h6 className="mb-0">{getUserProfile_c()?.name}</h6>
									</Link>
									<p style={{ color: "grey", fontSize: "11px" }}>{getUserProfile_c()?.role === CONSTANTS.ADMIN ? CONSTANTS.RESEARCH_ASSISTANT : CONSTANTS.DATA_COLLECTOR}</p>
								</div>
								<Button variant="danger" size="sm" onClick={() => logoutUser()}>
									<div className="row grid-divider"></div>
									Logout
								</Button>
							</>
						)
					}
					<ToastContainer />
				</Navbar.Collapse>
			</Container>
		</Navbar>
	);
};

export default Sidebar;
